<template>
  <div v-if="user">
    <br>

    <h3>
        Task 
    </h3>
    <b-table :fields="fields" :items="rTask">
      <template #cell(name)="data">
      <!--        selected   -->
      <b-button size="sm" variant="info" @click="selectTask(data.value)">{{ data.value }}</b-button>
    </b-table>

    <p> Selected Task : {{ selected }} </p>

    <br>

    <h3>
         
    </h3>
    <b-table :fields="fields" :items="wTask"></b-table>

    <br>

    <b-form-checkbox
      id="checkbox-1"
      v-model="status"
      name="checkbox-1"
      value="true"
      unchecked-value="false"
    >
         .()  
    </b-form-checkbox>
      <b-button v-b-toggle.collapse-1 variant="info"></b-button>
    <b-collapse id="collapse-1" class="mt-2">
      <b-card>
        <p class="card-text">
                   .    .
              .
        </p>
      </b-card>
    </b-collapse>
    <br>

    <ul>
      <li>
        <button type="submit" @click="registerTask">TASK </button>
      </li>
      <li>
        <button type="submit" class="red" @click="backPage"> </button>
      </li>
    </ul>
    <ul>
      <p> ED's DBD</p>
    </ul>
  </div>
</template>

<script>
export default {
  created() {
      this.$http.post('/api/getRTable', this.$route.params.userID)
          .then((res) => {
              this.rTask = res.data.rTask;
              this.wTask = res.data.wTask;
          })
          .catch((err) => {
              console.error(err)
          })
  },
  data () {
      return {
            selected: '',
            fields: [
                {key: 'name', label: 'TASK '},
                {key: 'period', label: '  '},
                {key: 'description', label: ''}
            ],
            rTask: [],
            wTask: [],
            status: 'false'
      }
  },
  methods: {
    backPage() {
      this.$router.push({name: "Submitter"});
    },
    selectTask(value) {
        this.selected = value;
    },
    registerTask() {
      if (this.selected != '' && this.status == 'true') {
        this.$http.post('/api/registerTask', {taskName: this.selected, userID: this.$route.params.userID})
          .then((res) => {
            console.log("register Task")
          })
          .catch((err) => {
            console.error(err)
          });

        alert(this.selected + "  .");

        //   
        this.$http.post('/api/getRTable', this.$route.params.userID)
          .then((res) => {
              this.rTask = res.data.rTask;
              this.wTask = res.data.wTask;
          })
          .catch((err) => {
              console.error(err)
          });
      }
      else {
        if (this.selected == '') {
          alert(" TASK .");
        }
        else {
          alert("  .");
        }
      } 
    }
  },
  computed:{
     user() {return this.$store.getters.user;}  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  position : relative;
  top : 80px;
  list-style-type : none;
  padding : 0;
}
ul > li {
  font-size : large;
  display : inline-block;
  margin : 0 80px;
  font-weight : bold;
}
a {
  text-decoration : none;
}
a > div {
  margin : 0px;
  height : 100px;
  width : 100px;
  border-color : #42b983;
  text-align: center;
}
ul > p {
  position : relative;
  top : 300px;
  padding-bottom : 40px;
  font-size : small;
}
.red{background-color: tomato!important;}
.red:hover{background-color: rgba(255, 99, 71, 0.849)!important;}
button[type=submit]{background-color: #247e70;color: white;padding: 14px 20px;margin: 8px 0;border: none;border-radius: 4px;cursor: pointer;}
button[type=submit]:hover{background-color: #257e70ad;}
</style>


